.Our_work_container {
  display: flex;
  height: 495vh;
  position: sticky;
  margin-top: -10rem;
  z-index: 100;
  align-items: center;
  justify-content: center;
  /* width: 100vw; */
  /* border: 2px solid yellow; */
  /* Set the desired height for the container */
}

.sticky-div {
  /* width: 30%; */
  /* Set the width for the sticky div */
  position: sticky;
  top: 0;
  background: linear-gradient(
    236.42deg,
    #8105e1 30.05%,
    rgba(0, 124, 124, 0) 127.3%
  ); /* Set the desired background color */
  padding: 20px;
  width: 50%;
  /* border: 1px solid green; */
  /* overflow-y: auto; */
  /* height: 100%; */
}
.OurWork_project {
  /* box-sizing: border-box; */
  width: 300px;
  /* Adjust the width and height of the container to match your desired border size */
  height: 200px;
  border: 1px solid #000;
  background-size: cover;
  /* Adjust the image size to cover the container */
  background-position: center;
  /* Adjust the image position within the container */
  background-repeat: no-repeat;
}
.our_work_scrollable-div {
  width: 50vw;
  /* Set the width for the scrollable div */
  padding: 20px;
  overflow-y: hidden;
  /* overflow-y: auto; */
  position: relative;
  background-color: var(--main-colour);
  margin-left: 40rem;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  /* border: 2px solid red; */
}
.ourWork_Scroll {
  color: white;
}

.OurWork_text {
  /* margin-top: 2%; */
  /* text-align: center; */
  font-family: "Encode Sans";
  color: white;
  font-size: 40px;
  line-height: 50px;
  margin-top: 50px;
  padding: 20px;
}
.OurWork_project {
  width: 40rem;
  height: 20rem;
  border-radius: 5%;
  border: 2px solid #8105e1;
  margin-bottom: 6rem;
  /* display: flex; */
  background-size: cover;
  /* Adjust the image size to cover the container */
  background-position: center;
  /* Adjust the image position within the container */
  background-repeat: no-repeat;
}
.OurWork_project img {
  width: 100%;
  height: 100%;
  border-radius: 5%;
}

/* DIV 2 */
.Our_work_container2 {
  /* border: 1px solid red; */
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  display: none;
}

.ourwork_body_text {



      display: flex;
        gap: -3%;
        /* position: absolute; */
        gap: 2%;
        margin-top: -10%;
        text-align: center;
        justify-content: center;
        /* width: 98%; */
        /* width: 40rem; */
        align-items: center;
        /* justify-content: end; */

}
.ourwork_small_text {
  display: block;
  background-color: #8105e1;
  border-radius: 10%;
  padding: 1%;
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(5px);
  -webkit-backdrop-filter: blur(5px);
  /* border: 1px solid rgba(255, 255, 255, 0.3); */
  background: rgba(87, 6, 151, 0.48);
}
@media screen and (max-width: 1310px) {
  .our_work_scrollable-div {
    margin-left: 32rem;
  }
  .OurWork_project {
    width: 35rem;
    height: 18.5rem;
  }
}
@media screen and (max-width: 1120px) {
  .our_work_scrollable-div {
    margin-left: 28rem;
  }
  .OurWork_project {
    width: 30rem;
    height: 15rem;
  }
}
@media screen and (max-width: 1000px) {
  .Our_work_container {
    display: none;
  }
  .Our_work_container2 {
    display: flex;
  }
}
@media screen and (max-width: 510px) {
  .OurWork_project {
    width: 80%;
    /* height: 50%; */
  }
}
